import React, {useState} from "react";

export default {
    title: 'Clock ',

}


export const Clock = () => {


    const [date, setDate] = useState<Date>(new Date((Date.now())))


    const bla =()=> {
        return setDate(data=>data)
    }

    const start = () => {
        setInterval(() => {
            setDate(bla())
        }, 1000)

    };


    return (
        <div>
             <span>
                   {date.getHours()}
                </span>
:
                <span>
                   {date.getMinutes()}
                </span>
            :
            <span>
                   {date.getSeconds()}
                </span>


            <div>
                <button
                    onClick={start}
                >
                    start
                </button>

            </div>
        </div>
    )
};




